import React, { useEffect, useRef } from 'react';
import {
  Content,
  ContentLine,
  ContentLineText,
  ContentRow,
  CustomIconButton,
  CustomTextButton,
  LeftContent,
  RightContent,
} from '@components/Playbar/styles';
import UseSelectOptionGateRange from '@hooks/UseSelectOptionGateRange';
import { DisplaySection } from '@typings/db';
import Sortable from 'sortablejs';

interface DisplaySectionSettingProps {
  displaySections: DisplaySection[];
  handleSettingChange: (index: number, field: string, value: string) => void;
  handleSettingAdd: () => void;
  handleSettingDelete: (index: number) => void;
  handleSettingOrderChange: (newSections: DisplaySection[]) => void;
}

const DisplaySectionSetting: React.FC<DisplaySectionSettingProps> = ({
  displaySections,
  handleSettingChange,
  handleSettingAdd,
  handleSettingDelete,
  handleSettingOrderChange,
}) => {
  const rightContentRef = useRef<HTMLDivElement>(null); // Ref 생성

  // Sortable 초기화 및 설정
  useEffect(() => {
    if (rightContentRef.current) {
      const sortable = Sortable.create(rightContentRef.current, {
        onEnd: (evt) => {
          const newSections = [...displaySections];
          if (evt.oldIndex != null && evt.newIndex != null) {
            const [movedItem] = newSections.splice(evt.oldIndex, 1);
            newSections.splice(evt.newIndex, 0, movedItem);
            handleSettingOrderChange(newSections);
          }
        },
      });
      return () => {
        sortable.destroy();
      };
    }
  }, [displaySections, handleSettingOrderChange]);

  return (
    <Content>
      <ContentRow>
        <LeftContent>
          <Content>
            <ContentLineText>Display Sections</ContentLineText>
            <CustomTextButton onClick={handleSettingAdd}>Add New</CustomTextButton>
          </Content>
        </LeftContent>
        <RightContent ref={rightContentRef}>
          {displaySections.map((displaySection, index) => {
            return (
              <ContentLine key={`${displaySection.start}-${displaySection.end}-${index}`}>
                {/*<ContentLine key={`${index}`}>*/}
                <UseSelectOptionGateRange
                  handleSettingChange={(field, value) => handleSettingChange(index, field, value)}
                  displaySection={displaySection}
                />
                <CustomIconButton onClick={() => handleSettingDelete(index)}>
                  <i className="fa-solid fa-xmark"></i>
                </CustomIconButton>
              </ContentLine>
            );
          })}
        </RightContent>
      </ContentRow>
    </Content>
  );
};

export default DisplaySectionSetting;
