import React, { useEffect, useRef } from 'react';
import {
  Content,
  ContentLine,
  ContentLineText,
  ContentRow,
  CustomIconButton,
  CustomTextButton,
  LeftContent,
  RightContent,
} from '@components/Playbar/styles';
import Sortable from 'sortablejs';
import UseSelectOptionData from '@hooks/UseSelectOptionData';
import { SectionData } from '@typings/db';

interface SectionDataSettingProps {
  sectionDatas: SectionData[];
  handleSettingChange: (index: number, value: string) => void;
  handleSettingOrderChange: (newSections: SectionData[]) => void;
  handleSettingAdd: () => void;
  handleSettingDelete: (index: number) => void;
}

const SectionDataSetting: React.FC<SectionDataSettingProps> = ({
  sectionDatas,
  handleSettingChange,
  handleSettingOrderChange,
  handleSettingAdd,
  handleSettingDelete,
}) => {
  const rightContentRef = useRef<HTMLDivElement>(null); // Ref 생성

  useEffect(() => {
    if (rightContentRef.current) {
      const sortable = Sortable.create(rightContentRef.current, {
        onEnd: (evt) => {
          const newSections = [...sectionDatas];
          if (evt.oldIndex !== undefined && evt.newIndex !== undefined) {
            const [movedItem] = newSections.splice(evt.oldIndex, 1);
            newSections.splice(evt.newIndex, 0, movedItem);
            handleSettingOrderChange(newSections);
          }
        },
      });
      return () => {
        sortable.destroy();
      };
    }
  }, [sectionDatas, handleSettingOrderChange]);

  return (
    <Content>
      <ContentRow>
        <LeftContent>
          <Content>
            <ContentLineText>Section Datas</ContentLineText>
            <CustomTextButton onClick={handleSettingAdd}>Add New</CustomTextButton>
          </Content>
        </LeftContent>
        <RightContent ref={rightContentRef}>
          {sectionDatas.map((sectionData, index) => {
            return (
              <ContentLine key={`${sectionData}-${index}`}>
                {/*<ContentLine key={index + 1}>*/}
                <UseSelectOptionData
                  handleSettingChange={(value) => handleSettingChange(index, value)}
                  sectionData={sectionData}
                />
                <CustomIconButton onClick={() => handleSettingDelete(index)}>
                  <i className="fa-solid fa-xmark"></i>
                </CustomIconButton>
              </ContentLine>
            );
          })}
        </RightContent>
      </ContentRow>
    </Content>
  );
};

export default SectionDataSetting;
